@use 'scroll';
@use 'stack';


:root {
	@include scroll.custom-property;
	@include scroll.custom-property-appearance-bar;
	// @include scroll.custom-property-appearance-tab;

	@include stack.custom-property;
	@include stack.custom-property-appearance-bar;
	// @include stack.custom-property-appearance-tab;
}

div.tab-scroll {
	@include scroll.main;
	@include scroll.appearance-bar;
	// @include scroll.appearance-tab;

	@media print {
		@include scroll.print;
	}
}

div.tab-stack {
	@include stack.main;
	@include stack.appearance-bar;
	// @include stack.appearance-tab;

	@media print {
		@include stack.print
	}
}
